            margin-left: 93px;
        }
    
        .alteration-info-table-row1 > td:nth-child(3){
            margin-left: 93px;
        }
    
        .alteration-info-table-row4 > td:first-child{
            padding-right: 8px;
        }
    
        .alteration-info-table-row5 > td:nth-child(3){
            margin-right: 8px;
        }
    
        .alteration-info-table-row5 > td:nth-child(4) > input{
            width: 200px
        }
    
        .alteration-info-table-row5{
            padding-top: 10px;
        }
    
        .alteration-info-table-row5 td:first-child{
            padding-right: 12px;
        }
    
        .alteration-info-table-row5 td:nth-child(2){
            padding-right: 100px;
        }
    }
    
    .form-label {
        display: block;
        margin-bottom: 5px;
        font-weight: bold;
    }
    
    .form-input, .form-select {
        width: 100%;
        padding: 8px 10px;
        margin-bottom: 20px;
        border: 1px solid #ddd;
        border-radius: 4px;
    }
    
    .form-button {
        width: 100%;
        padding: 10px;
        background-color: #06407A;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
    }
